<script>
  import oiModal from '@/module/modal/comp/Modalx.vue'

  export default {
    name: 'rulesDensity',

    components: { oiModal }
  }
</script>

<template>
  <span>
  <oiModal id="rulesDensityModal">
            <h4 style="margin-top:0px; text-align:center"><strong>Regras para clculo de 'Densidade'</strong></h4>
            
            <p style="text-align:left">
                Defeitos fechados de CONSTRUO, divididos pelos CTs fechados diferentes de MASSA, multiplicados por 100.
            </p>
            <p style="text-align:left">
                Devido a este painel exibir os dados em uma rgua de tempo, totalizamos os defeitos relacionados aos CTs, no ms de execuo destes CTs.
            </p>
            <p style="text-align:left">
                Em outros locais onde os dados no so exibidos em uma rgua de tempo, no consideramos o relacionamento entre Defeitos e CTs. Isto , contemplamos tambm defeitos avulsos (sem relacionamento com CTs).
            </p>
            <p style="text-align:left">
                Os valores de referncia: Aceitvel, Moderada e Alta, so respectivamente: 10%, 20% e 30%.
            </p>
        </oiModal>  
  </span>   
</template>



